.Input-email {
    width: inherit;
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue";
    font-weight: 200;
    font-size: 28px;
    color: black;
    border-color: rgb(238, 238, 238);
    border-style: solid;
    border-top: unset;
    border-left: unset;
    border-right: unset;
    border-width: 2px;
    outline: none;
}

::-webkit-input-placeholder {
    color: #ACACAC;
}

::-ms-input-placeholder {
    color: #ACACAC;
}

::placeholder {
    color: #ACACAC;
}

.Input-message {
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue";
