    font-size: 28px;
    color: black;
    border-width: 2px;
    border-radius: 10px;
    border-color: rgb(238, 238, 238);
    border-style: solid;
    padding: 20px;
    outline: none;
    height: 186px;
    text-align: start;
    resize: none;
    width: 620px;
}

.Attach-button {
    font-family: "HelveticaNeue-Medium", "Helvetica Neue Medium", "Helvetica Neue";
